<template>
  <b-modal v-model="visible" centered size="lg">
    <template #modal-header>
      <div class="text-right w-100 d-flex flex-row justify-content-between">
        <h4>User</h4>
        <b-button v-if="purpose !== 'watch'" @click="deleteUser" variant="danger"><span style="margin-right: 7px">Delete</span><i class="fa-solid fa-trash"></i></b-button>
      </div>
    </template>

    <b-row>
      <b-col sm="6">
        <b-form-group label="Name">
          <b-form-input v-if="purpose === 'edit'"
                        v-model="user.name"
                        vuelidate
                        :class="{error: v$.user.name.$error}">
          </b-form-input>

          <b-form-input v-else-if="purpose === 'watch'"
                        v-model="user.name"
                        :class="{error: v$.user.name.$error}"
                        disabled>
          </b-form-input>

          <vuelidate :v-model="v$.user.name" />
        </b-form-group>
      </b-col>

      <b-col sm="6">
        <b-form-group label="E-mail">
          <b-form-input v-if="purpose === 'edit'"
                        v-model="user.email"
                        vuelidate
                        :class="{error: v$.user.email.$error}">
          </b-form-input>

          <b-form-input v-else-if="purpose === 'watch'"
                        v-model="user.email"
                        disabled>
          </b-form-input>
          <vuelidate :v-model="v$.user.email" />
        </b-form-group>
      </b-col>

      <b-col sm="6">
        <b-form-group label="Responsible">
          <b-checkbox v-if="purpose === 'edit'"
                        v-model="user.responsible"
                        switch>
          </b-checkbox>

          <b-checkbox v-else-if="purpose === 'watch'"
                        v-model="user.responsible"
                        switch
                        disabled>
          </b-checkbox>
        </b-form-group>
      </b-col>

      <b-col sm="6">
        <b-form-group label="Super user">
          <b-checkbox v-if="purpose === 'edit'"
                      v-model="user.superUser"
                      switch>
          </b-checkbox>

          <b-checkbox v-else-if="purpose === 'watch'"
                      v-model="user.superUser"
                      switch
                      disabled>
          </b-checkbox>
        </b-form-group>
      </b-col>
    </b-row>

    <template #modal-footer>
      <div v-if="purpose === 'edit'">
        <b-button variant="secondary" @click="close" style="margin-right: 10px">Cancel</b-button>
        <b-button variant="success" type="submit" @click="submit">Update</b-button>
      </div>
      <div v-else-if="purpose === 'watch'">
        <b-button variant="secondary" @click="close" style="margin-right: 10px">Close</b-button>
      </div>
    </template>
  </b-modal>
</template>

<script>
import {useVuelidate} from "@vuelidate/core";
import {required, minLength, sameAs} from "vuelidate/lib/validators";
import {userService} from "@/services/userService";
import Vuelidate from "@/components/Vuelidate.vue";
import {email, requiredIf} from "@vuelidate/validators";
import {withParams} from "vuelidate";

export default {
  setup(){
    return {v$: useVuelidate()}
  },
  components: {
    Vuelidate
  },
  props: {
  },
  data(){
    return {
      visible: false,
      purpose: 'watch',
      user: {
        type: Object,
        default: ()=> {}
      },
      defaultUser: {
        id: '2d210a74-419d-4705-a1ec-820274894c6b',
        name: '',
        email: '',
        password: '',
        responsible: false,
        superUser: false,
        createDate: new Date().toISOString()
      },
      repeatPassword: '',

    }
  },
  validations(){
    return {
      user: {
        name: {
          required
        },
        email: {
          required,
          email
        },
      },
    }
  },
  methods: {
    show(purpose, user){
      this.purpose = purpose
      this.visible = true
      this.user = user != null ? user : this.defaultUser
      this.repeatPassword = this.user.password
    },

    close(){
      this.visible = false
      this.v$.$reset()
    },

    deleteUser(){
      this.$bvModal.msgBoxConfirm(`Are you sure you want to delete ${this.user.name}? It cannot be undone!`, {
        title: 'Delete user',
        size: 'sm',
        buttonSize: 'sm',
        okVariant: 'danger',
        centered: true
      })
          .then(result => {
              if(result === true){
                userService.deleteUser(this.user.id)
                    .then(result => {
                      this.$emit('delete-user',result.data)
                      this.close()
                    })
                    .catch(error => this.$bvModal.msgBoxOk(error.status, {
                      title: 'Error',
                      size: 'sm',
                      buttonSize: 'sm',
                      okVariant: 'success',
                      centered: true
                    }))
              }else{
                console.log("cancel deletion")
              }
            }
          )
    },

    submit(){
      this.v$.$touch()
      if(this.v$.$errors.length === 0){
          this.createDate = new Date().toISOString()
          userService.updateUser(this.user, this.user.id)
              .then(result => {
                this.user = result.data
                this.close()
              })
              .catch(error => this.$bvModal.msgBoxOk(error.status, {
                title: 'Error',
                size: 'sm',
                buttonSize: 'sm',
                okVariant: 'success',
                centered: true
              }))
      }else{
        console.log(this.v$.$errors)
      }

    }
  },
  computed: {
  },
  watch: {
    'this.v$.user.name.$errors'(){
      console.log("ting")
    }
  },
  mounted(){

  }
}
</script>

<style>
.error {
  border: 1px solid red;
}
</style>