<template>
    <div id="app">
        <nav>
            <b-navbar toggleable="lg" type="dark" variant="secondary">
                <b-container fluid class="w-75">
                    <b-navbar-brand href="#">
                        <img src="/img/logos/Bolls.png" class="bolls-logo">
                    </b-navbar-brand>

                    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

                    <b-collapse id="nav-collapse" is-nav>
                        <b-navbar-nav>
                            <b-nav-item><router-link to="/">Dashboard</router-link></b-nav-item>
                            <b-nav-item><router-link to="/instruments">Instruments</router-link></b-nav-item>
                            <b-nav-item><router-link to="/templates">Templates</router-link></b-nav-item>
                        </b-navbar-nav>

                        <!-- Right aligned nav items -->
                        <b-navbar-nav class="ml-auto">
                            <b-nav-item class="notification-box" v-if="$store.state.isAuthenticated.responsible">
                                <router-link to="/notifications">
                                    <i class="fa-solid fa-bell notification-bell"></i>
                                    <span class="badge-buble" v-if="notifications.filter(not => not.hasReacted === false).length > 0">{{notifications.filter(not => not.hasReacted === false).length}}</span>
                                </router-link>
                            </b-nav-item>

                            <b-nav-item-dropdown right v-if="$store.state.isAuthenticated !== null && $store.state.isAuthenticated.responsible">
                                <!-- Using 'button-content' slot -->
                                <template #button-content>
                                    <em>User</em>
                                </template>
                                <b-dropdown-item><router-link to="/settings" style="color: #212529; font-weight: 200">Settings</router-link></b-dropdown-item>
                                <b-dropdown-item v-if="$store.state.isAuthenticated.superUser"><router-link to="/super-user" style="color: #212529; font-weight: 200">Super user</router-link></b-dropdown-item>
                                <b-dropdown-item href="#" @click="signout">Sign Out</b-dropdown-item>
                            </b-nav-item-dropdown>
                            <b-nav-item v-else><router-link to="/signin">Sign In</router-link></b-nav-item>
                        </b-navbar-nav>
                    </b-collapse>
                </b-container>
            </b-navbar>
        </nav>
        <router-view />
    </div>
</template>

<script>
import {mapActions} from "vuex";
import {authService} from "@/services/authService";
import {notificationService} from "@/services/notificationService";

export default {
  name: 'App',
  created() {
    this.checkAuthentication()
  },
  data(){
    return {
      notifications: []
    }
  },
  methods: {
    ...mapActions(['checkAuthentication']),
    signout(){
      authService.logout().then(result => window.location.href="/")
          .catch(error => console.log(error))
    }
  },
  mounted() {
    notificationService.getAll()
        .then(result => this.notifications = result.data)
        .catch(error => console.error(error))
  }
}
</script>

<style lang="scss">
body, html {
  background-color: #F7F7F7;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background-color: #F7F7F7;
}

nav {

  a, .nav-link {
    font-weight: 600;
    color: white;
    text-decoration: none;
    font-size: 18px;

    &.router-link-exact-active {
      color: #68DBFF;
    }
  }

  a:hover, .nav-link:hover {
    color: #BFC1C1;
    text-decoration: none;
  }

  .bolls-logo {
    height: 50px;
    width: 50px;
  }

  .notification-box {
    position: relative;
    padding: 0px 10px;
  }

  .notification-bell {
    font-size: 22px;
    color: white;
  }

  .badge-buble {
    position: absolute;
    top: -6px;
    right: 0px;
    padding: 1px 8px;
    border-radius: 50%;
    background-color: red;
    color: white;
    font-size: 14px;
  }
}
</style>
